.AboutPage {
  display: flex;
  align-items: center;
  width: 100vmax;
  height: 100vh;
  grid-template-rows: 200px 200px 200px 200px;
  background: linear-gradient(
    251.97deg,
    #2662bc 31.29%,
    rgba(35, 164, 128, 0.973698) 59.4%,
    rgba(32, 222, 74, 0.95) 91.94%
  );
}


p, ul, li {
margin: 2vh 1.5vh;
font-family: Arial, Helvetica, sans-serif;
font-size: 3vh;
color: #000000;
text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
line-height: 100%;
}



li {
margin-left:5vh;
}

.teamHeading {
font-family: "Codystar";
color: white;
padding: 3vh 0vh 0vh 8vw;
font-size: 6.5vh;
padding-bottom: 2.5vh;
}