/* latin-ext */
@font-face {
  font-family: "Codystar";
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/codystar/v15/FwZY7-Q1xVk-40qxOu6J6MmTpA.woff2)
    format("woff2");
  unicode-range: U+0100-02AF, U+0304, U+0308, U+0329, U+1E00-1E9F, U+1EF2-1EFF,
    U+2020, U+20A0-20AB, U+20AD-20CF, U+2113, U+2C60-2C7F, U+A720-A7FF;
}
/* latin */
@font-face {
  font-family: "Codystar";
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/codystar/v15/FwZY7-Q1xVk-40qxOu6H6Mk.woff2)
    format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA,
    U+02DC, U+0304, U+0308, U+0329, U+2000-206F, U+2074, U+20AC, U+2122, U+2191,
    U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

.login_page {
  align-items: center;
  width: 100vmax;
  height: 100vh;
  grid-template-rows: 200px 200px 200px 200px;
  background: linear-gradient(
    251.97deg,
    #2662bc 31.29%,
    rgba(35, 164, 128, 0.973698) 59.4%,
    rgba(32, 222, 74, 0.95) 91.94%
  );
}

.loginHeading {
  font-family: "Codystar";
  color: white;
  padding: 20vh 0vh 0vh 25vw;
  font-size: 6.5vh;
}

.form{
  padding: 2vh 0vh 0vh 25vw;
}
